.User {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.User .Cell__main {
  padding-right: 0;
}
.body {
  
}
.clickBtn {
  width: 90px;
  height: 50px;
  color: black;
  border-radius: 5px;
  background-color: white;
  box-shadow: 0px 2px 5px gray;
  display: inline-block;
  position: relative;
  color: black;
  text-decoration: none;
  padding: .5em .6em .4em .6em;
  border-radius: 8px;
  outline: none;
  box-shadow:
   rgba(0,0,0,.2) 0 .2em 6px;
}
.clickBtn:active {
  top: .2em;
  box-shadow:
   rgba(0,0,0,.3) 0 .1em 1px,
   rgba(0,0,0,.3) 0 .3em 6px;
}
.moneyInfo {
  width: 100px;
  height: 60px;
  color: black;
  border-radius: 5px;
  background-color: gainsboro;
}
.parentDiv {
display: flex;
flex-direction: row;
justify-content: center;
}
.miningInfo {
  width: 250px;
  height: 60px;
  color: black;
  border-radius: 5px;
  background-color: gainsboro;
  display: flex;
  margin-left: 50px;                    
}
.textInfo {
  color: black;
  font-size: 45px;
  text-align: center;
}